import React from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";

import { AuthProvider } from "./contexts/AuthContext";
import Navigation from "./components/Navigation"; //    
import TabNavigation from "./components/TabNavigation"; //   
// import PrivateRoute from "../src/components/PrivateRoute"; //  
import UploadPage from "./pages/UploadPage";
import ProductionBudget from "./pages/ProductionBudget";
import ProductionSupply from "./pages/ProductionSupply";
import ProductionRepair from "./pages/ProductionRepair";
import ProductionQuality from "./pages/ProductionQuality";

function App() {
  return (
    <AuthProvider>
      <Router basename="/Inotex-analytics">
        <Routes>
          {/*   */}
          <Route path="/" element={<Navigation />} />

          {/*  ""    */}
          <Route
            path="/production/*"
            element={
              <div>
                <TabNavigation /> {/*  "" */}
                <Routes>
                  <Route path="summary" element={<UploadPage />} />
                  <Route
                    path="budget"
                    element={<ProductionBudget />} //   
                  />
                  <Route
                    path="supply"
                    element={<ProductionSupply />} //   
                  />
                  <Route
                    path="repair"
                    element={<ProductionRepair />} //   
                  />
                  <Route
                    path="quality"
                    element={<ProductionQuality />} //   
                  />
                </Routes>
              </div>
            }
          />

          {/*    */}
          <Route path="*" element={<div>  </div>} />
        </Routes>
      </Router>
    </AuthProvider>
  );
}

export default App;
